/* Register.css */

.Register {
    position: relative;
    height: 100vh; /* Full viewport height */
    margin: 0;
    overflow: hidden;
  }
  
  .background-animation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1; /* Ensure background is behind other elements */
  }
  
  .register-form-container {
    position: absolute; /* Use absolute positioning within Register */
    top: 50%; /* Center vertically */
    left: 50%; /* Center horizontally */
    transform: translate(-50%, -50%); /* Centering correction */
    z-index: 1; /* Ensure form is above the background */
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 500px;
    height: 100%;
  }
  
  .register-form {
    margin: auto;
    background: rgba(255, 255, 255, 0.705); /* Semi-transparent white */
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.575);
    width: 100%;
    max-width: 500px; /* Adjust as needed */
  }
  
  h1{
    text-align: center;
    font-family: Helvetica;
    font-size: 30px;
    font-weight: bold;
  }

  h2{
    text-align: center;
    font-family: Helvetica;
    font-size: 14px;
  }

  button{
    background-color: rgba(0, 0, 0, 0.904);
    color: white;
    border: none;
    border-radius: 10px;
    padding: 10px 20px;
    cursor: pointer;
    margin: 10px 0;
    width: 100%;
  }

  label{
    font-weight: Bold;
    font-family: Helvetica;
    color: rgb(41, 41, 41);
  }

  input{
    margin-bottom: 15px;
    background-color: transparent;
    border: 2px solid rgba(0, 0, 0, 0.301);
    padding: 5px;
    border-radius: 10px;
    text-indent: 10px;
    width: 100%;
  }

  input::placeholder{
    color: rgba(0, 0, 0, 0.699);
  }

  .inp_div{
    width: 100%;
  }

  p{
    text-align: center;
    font-family: Helvetica;
    font-size: 15px;
    color: rgba(0, 0, 0, 0.904);
  }

  .side-content {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 50%;
    height: 60%0%;
    background: rgba(0, 0, 0, 0.904);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }